/* src/App.css */

/* 기본 레이아웃 설정 */
.App {
  font-family: Arial, sans-serif;
  width: 98%;
  height: 95vh;
  margin: 0 auto;
  padding: 1rem;
  background-color: #f4f4f4;
}

/* 제목 스타일 */
.App h1 {
  text-align: center;
  font-size: 2rem;
  color: #333;
  margin-bottom: 0.5rem;
}

/* HeroSelector와 TabBar 간격 */
.App > div {
  margin-bottom: 0.5rem;
}

/* 능력 카드 섹션 */
.abilities-section {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-around;
}

/* 로딩 중일 때 보여줄 텍스트 */
.loading-text {
  text-align: center;
  color: #888;
}
